import React, { Component, useState } from 'react'
import { toHumanReadable, toAbi } from '../../../contract/index'



export default function AbiToHumanReadable() {
    let [abi, setAbi] = useState('');
    let hrabi = toHumanReadable(abi)
    let [Rabi, setRAbi] = useState('');
    let Rhrabi = toAbi(Rabi)
    return (
        <div>
            <div className="box box-primary">
                <div className="box-header with-border">
                    <h3 className="box-title">ABI 人類可讀格式轉換</h3>
                </div>
                <div className="box-body" >
                    <textarea
                        className="form-control"
                        placeholder="ABI"
                        rows="20"
                        onChange={(e) => {
                            try {
                                setAbi(JSON.parse(e.target.value))
                            } catch (e) {

                            }
                        }}
                    ></textarea>
                    <textarea
                        className="form-control"
                        placeholder="Human-Readable ABI"
                        rows="10"
                        value={hrabi}
                        disabled
                    ></textarea>
                </div>
            </div>
            <div className="box box-primary">
                <div className="box-header with-border">
                    <h3 className="box-title">ABI 人類可讀格式轉換</h3>
                </div>
                <div className="box-body" >
                    <textarea
                        className="form-control"
                        placeholder="Human-Readable ABI"
                        rows="20"
                        onChange={(e) => {
                            try {
                                setRAbi(JSON.parse(e.target.value))
                            } catch (e) {

                            }
                        }}
                    ></textarea>
                    <textarea
                        className="form-control"
                        placeholder="ABI"
                        rows="10"
                        value={Rhrabi}
                        disabled
                    ></textarea>
                </div>
            </div>
        </div>
    )
}

export { AbiToHumanReadable }